* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Open Sans', sans-serif;
    color: #757371
}

h1 {
    font-size: 24px;
}

h2 {
    font-size: 22px;
}

h3 {
    font-size: 20px;
}

h4 {
    font-size: 18px;
}

h5 {
    font-size: 17px;
}

h6 {
    font-size: 16px;
    color: #3c6ebe;
    font-weight: 600;
}

p {
    font-size: 13px;
}

label {
    font-size: 11px;
    font-weight: 600;
}

@media (min-width: 768px) {
    label {
        font-size: 13px;
        font-weight: 700;
    }
  }

.notify-body {
    background-color: #00B446;
}